/* App.css */

/* General Reset and Styling */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: #f9fafb;
  color: #333;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 1rem;
}

/* Container for the entire app */
.App {
  max-width: 1200px;
  width: 100%;
  margin: auto;
  padding: 2rem;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

/* Heading Styles */
h1 {
  text-align: center;
  color: #00c2e8;
  font-size: 2rem;
  margin-bottom: 2rem;
  font-weight: bold;
}

h2 {
  text-align: center;
  margin-bottom: 1.5rem;
  color: #333;
  font-size: 1.5rem;
  border-bottom: 2px solid #00c2e8;
  padding-bottom: 0.5rem;
  margin-bottom: 1rem;
}

h3 {
  font-size: 1.1rem;
  color: #555;
  margin-bottom: 0.5rem;
}

/* Flexbox for Columns */
.flex-container {
  display: flex;
  justify-content: space-between;
  gap: 1.5rem;
}

/* Form Section Styling */
.form-section {
  flex: 1;
  background-color: #f1f5f9;
  border-radius: 8px;
  padding: 1rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

/* Example adjustments */
.form-section {
  padding: 1rem;
  max-width: 800px;
  margin: 0 auto;
}

.card {
  margin-bottom: 1rem;
  padding: 1rem;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.button-group {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin-top: 1rem;
}


/* Input Styling */
input[type="text"] {
  display: block;
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  color: #333;
  background-color: #fff;
  transition: all 0.2s ease;
}

input[type="text"]:focus {
  border-color: #00c2e8;
  box-shadow: 0 0 8px rgba(0, 194, 232, 0.3);
  outline: none;
}

/* Card Styling */
.card {
  background-color: #ffffff;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #e0e7ff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05); /* Subtle shadow */
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  margin-bottom: 1.5rem; /* Adds vertical spacing between boxes */
}

.card:hover {
  transform: translateY(-3px);
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.1); /* Stronger shadow on hover */
}

/* Button styling */
.button-group {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 1rem;
}

button {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  color: #ffffff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  opacity: 0.9;
}

button:active {
  transform: scale(0.98);
}

/* Specific styles for Add and Remove buttons */
.add-button {
  background-color: #28a745; /* Green for Add */
}

.remove-button {
  background-color: #dc3545; /* Red for Remove */
}

/* Specific styles for Generate button */
.generate-button {
  background-color: #007bff; /* Blue color */
  color: white;
  margin-top: 1.5rem;
  padding: 0.75rem 1.5rem;
  font-size: 1.1rem;
  font-weight: bold;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  display: block;
  width: fit-content;
  margin: 1.5rem auto; /* Centers the button */
}

.generate-button:hover {
  background-color: #0056b3;
}

.generate-button:active {
  transform: scale(0.98);
}
